import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';
import { AlgorithmImplementation } from '..';
export class Template extends DefinitionIndeCanvasCore {
    mouse: number[] = [0, 0];
    pts: number[][];
    r: number = 5;
    t: number = 0.0;
    constructor(AlgorithmImplementation: AlgorithmImplementation) {
        super(AlgorithmImplementation);
        this.indeCanvas.title = 'template';
        this.Init();
    }
    public Init() { this.mouse = [0, 0]; }
    public MouseMove(x: number, y: number) { this.mouse = [x, y]; }
    public RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        this.t = 0.01;
        ctx.beginPath();
        ctx.arc(this.mouse[0], this.mouse[1], Math.sin(this.t) * 3 + this.r, 0, Math.PI * 2);
        ctx.stroke();

    }
}