import { Canvas } from '../../Model/GraphicsCoreWrapper/CanvasWrapper/Canvas/Canvas';
import { ActiveCommandController } from './Core/ActiveCommandController';
import { GeomeetryCommon } from './Core/GeometryCommon';
import { DefinitionIndeCanvasCore } from './Core/DefinitionIndeCanvasCore';
// import { BallBounce } from './Definition/BallBounce';
// import { BallBounceClass } from './Definition/BallBounceClass';
// // import { GaussianDistribution } from '../SceneVis/Definition/GaussianDistribution';

import { GeometryInter } from './Definition/GeometryInter';
import { Template } from './Definition/Template';
import { Blank } from './Definition/Blank';

import { Implementation } from '..';
export class AlgorithmImplementation {
    implementation: Implementation;
    canvas: Canvas;
    activeCommandController: ActiveCommandController;
    geomeetryCommon: GeomeetryCommon;

    definitions: DefinitionIndeCanvasCore[] = [];

    constructor(implementation: Implementation) {
        this.implementation = implementation;
        this.canvas = implementation.app.model.graphicsCoreWrapper.canvasWrapper.canvas;

        this.activeCommandController = new ActiveCommandController(this);
        this.geomeetryCommon = new GeomeetryCommon(this);
        this.Init();
        this.InitDefinition();
    }
    public Init() {
        this.definitions = [];
    }

    get width() {
        return this.implementation.app.model.graphicsCoreWrapper.canvasWrapper.canvas.canvas.width;
    }
    get height() {
        return this.implementation.app.model.graphicsCoreWrapper.canvasWrapper.canvas.canvas.height;
    }
    InitDefinition() {
        for (let d of this.definitions) { d.Init(this.implementation.app.model.graphicsCoreWrapper.canvasWrapper.canvas.ctx); }
    }
    Render(ctx: CanvasRenderingContext2D) {
        // for (let d of this.definitions) {d.Render(ctx); }
    }
    MouseMove(x: number, y: number) {
        for (let d of this.definitions) { d.MouseMovePre(x, y); }
    }
    MouseLeftClick(x: number, y: number) {
        for (let d of this.definitions) { d.MouseLeftClickPre(x, y); }
    }
    MouseDown(x: number, y: number) {
        for (let d of this.definitions) { d.MouseDownPre(x, y); }
    }
    MouseUp(x: number, y: number) {
        for (let d of this.definitions) { d.MouseUpPre(x, y); }
    }
    MouseDrag(x: number, y: number) {
        for (let d of this.definitions) { d.MouseDragPre(x, y); }
    }
    MouseMiddleClick(x: number, y: number) {
        for (let d of this.definitions) { d.MouseMiddleClickPre(x, y); }
    }
    MouseRightClick(x: number, y: number) {
        for (let d of this.definitions) { d.MouseRightClickPre(x, y); }
    }
    MouseDoubleClick(x: number, y: number) {
        for (let d of this.definitions) { d.MouseDoubleClickPre(x, y); }
    }
    MouseWheel(x: number, y: number, v: number) {
        for (let d of this.definitions) { d.MouseWheelPre(x, y, v); }
    }
    Dispose() {
        this.definitions = [];
    }
    LoadDefinition(v: string) {
        //
    }
    public InitPost() {
        this.implementation.AddMenu('AlgorithmEnv', 'definition', 'blank', () => {
            this.definitions = [];
            const d0 = new Blank(this); d0.Dummy();
        });
        this.implementation.AddMenu('AlgorithmEnv', 'definition', 'Template', () => {
            this.definitions = [];
            const d0 = new Template(this); d0.Dummy();
        });
        this.implementation.AddMenu('AlgorithmEnv', 'definition', 'Geo Inter', () => {
            this.definitions = [];
            const d0 = new GeometryInter(this); d0.Dummy();
        });

    }
}